<template>
  <div class="search">
    <div class="top">
      <van-search
        v-model="value"
        placeholder="搜关键词~搜作者~"
        autofocus
        show-action
        shape="round"
        @search="onSearch"
      >
        <div slot="action" @click="onSearch">确定</div>
      </van-search>
    </div>
    <div class="history">
      <div class="tit">
        <div>搜索记录</div>
        <div @click="del">
          <van-icon name="delete" size="18px" />
        </div>
      </div>
      <div class="ions" v-if="historyList.length > 0">
        <span class="his_list" @click="keySearch(item)"  v-for="(item,i) in historyList"
          :key="i">{{item}}</span>
      </div>
      <div v-else class="noHis">暂无历史记录</div>
    </div>
  </div>
</template>

<script>
import "../assets/scss/search.scss";
import { mapState, mapActions } from "vuex";

export default {
  name: "Search",
  data() {
    return {
      value: "",
      historyList: [],
    };
  },
  created(){
    this.init();
  },
  computed: {
    ...mapState(["serchKey"])
  },
  methods: {
    ...mapActions(["setSerchKey"]),

    //  点击搜索框检索
    onSearch() {
      let { value, setSerchKey, historyPush } = this;
      if (value.trim() === "") {
        this.$toast("请输入内容再进行搜索!");
        return;
      }
      setSerchKey(value).then(() => {
        historyPush(value);
        this.$router.push({
          path: "/list",
        });
      });
    },
    // 删除记录
    del() {
      let that = this;
      if (that.historyList.length == 0) return;
     this.$dialog.confirm({
      
        message: "确定清空历史记录?",
      }).then(() => {
        that.historyList = [];
        window.localStorage.setItem("historyArr", JSON.stringify([]));
      });
    },

    // 历史记录新增
    historyPush(key) {
      if (key == "") return;
      let arr = JSON.parse(window.localStorage.getItem("historyArr"));
      if (arr && typeof arr === "object") {
        if (arr.indexOf(key) < 0) arr.push(key);
        window.localStorage.setItem("historyArr", JSON.stringify(arr));
      } else {
        let array = [];
        array.push(key);
        window.localStorage.setItem("historyArr", JSON.stringify(array));
      }
    },

    // 关键字搜索
    keySearch(key) {
      let { setSerchKey } = this;
      setSerchKey(key).then(() => {
        this.$router.push({
          path: "/list",
        });
      });
    },

    init() {
      let arr = JSON.parse(window.localStorage.getItem("historyArr"));
      if (arr) this.historyList = arr;
    }
  },
};
</script>

